import {masterchefContract} from "../utils/web3_http.js";
import {getFSTABLEPrice, getPoolLiquidity} from "./getData.js";
import BigNumber from 'bignumber.js'

const STABLE_PER_BLOCK = new BigNumber(0.5)
const BSC_BLOCK_TIME = 3
const BLOCKS_PER_YEAR = new BigNumber((60 / BSC_BLOCK_TIME) * 60 * 24 * 365) // 10512000
const STABLE_PER_YEAR = STABLE_PER_BLOCK.times(BLOCKS_PER_YEAR)

const getAllocPoint = async (pid) => {
  const res = await masterchefContract.methods.poolInfo(pid).call();
  return res.allocPoint;
};

let totalAlloc = 0;
const getTotalAllocPoint = async () => {
    if(totalAlloc === 0 )
    {
      totalAlloc = await masterchefContract.methods.totalAllocPoint().call();
    }
    return totalAlloc;
};

/**
 * Get the APR value in %
 * 
 * formula used:
 * poolAPR = (((poolAllocPoint/totalAllocPoint) * BLOCKS_PER_YEAR * Reward_TOKEN_Price) / poolLiquidtyInUSD) * 100
 */
 export const getPoolApr = async (pid) => {
    const poolLiquidityValue = await getPoolLiquidity(pid);
    if(poolLiquidityValue === 0) return '';

    const rewardTokenPrice = await getFSTABLEPrice();

    const allocPointVal = await getAllocPoint(pid);
    if(allocPointVal === 0) return 0;
    
    const totalAllocPointVal = await getTotalAllocPoint();
    const allocPoint = new BigNumber(allocPointVal.toString());
    const poolWeight = allocPoint.div(new BigNumber(totalAllocPointVal.toString()));
    const totalRewardPricePerYear = new BigNumber(rewardTokenPrice).times(poolWeight).times(BLOCKS_PER_YEAR);

    const apr = totalRewardPricePerYear.div(poolLiquidityValue).times(100)
    return apr.isNaN() || !apr.isFinite() ? null : Math.round(apr.toNumber());
  }

    //
    //formula used:
    //farmAPR = ((poolAllocPoint/totalAllocPoint) * TOKEN_MINTED_PER_YEAR * TOKEN_PRICE / poolLiquidtyInUSD) * 100
    //
    //
    export const getFarmApr = async (pid) => {
        const poolLiquidityValue = await getPoolLiquidity(pid);
        if(poolLiquidityValue === 0) return '';

        const allocPointVal = await getAllocPoint(pid);
        if(allocPointVal === 0) return 0;

        const totalAllocPointVal = await getTotalAllocPoint();
        const allocPoint = new BigNumber(allocPointVal.toString());

        const poolWeight = allocPoint.div(new BigNumber(totalAllocPointVal.toString()));
        const yearlyStableRewardAllocation = STABLE_PER_YEAR.times(poolWeight)

        const stablePriceUSD = await getFSTABLEPrice();

        const stableRewardsApr = yearlyStableRewardAllocation.times(stablePriceUSD).div(poolLiquidityValue).times(100)

        let stableRewardsAprAsNumber = null
        if (!stableRewardsApr.isNaN() && stableRewardsApr.isFinite()) {
            stableRewardsAprAsNumber = Math.round(stableRewardsApr.toNumber());
        }

        return stableRewardsAprAsNumber;
  }