import styles from "../css/Header.module.css";
import logo from "../assets/logo.png";
import menubtn from "../assets/menubtn.png";
import { useDispatch , useSelector} from "react-redux";
import {connect, readPublicKey} from "./integeration.js";
import React, { useState } from "react";

function Header(props, state) {
  
  const web3=useSelector(state => state.web3)
  const publicKey=useSelector(state=>state.publicKey)
  const [errorMessage,setErrorMessage]=useState("");
  const [acounts,setAcounts]=useState("")
  var text="Connect";
  
  if(web3!=null)
  {
  text="Disconnect";
  }
  const dispatch = useDispatch();
  
  
  return (
    <div className={styles.bgcolor}>
    <div className={styles.row}>
      <div className={styles.c1}>
        <div className={styles.row1}>
          <div className={styles.c11}>
            <button className={styles.menubtn} onClick={()=>{dispatch({ type: "menuShowChange"})}}>
              <img src={menubtn} className={styles.icon}></img>
            </button>
          </div>
          <div className={styles.c22}>
            <img src={logo} className={styles.logo}></img>
          </div>
        </div>
      </div>
      <div className={styles.c2}>
        <div className={styles.row1}>
          <div className={styles.c11}>
          <div className={styles.address}>{publicKey}</div>
          </div>
          <div className={styles.c22}><button className={styles.connect} onClick={()=>{
            
        if(text=="Connect")
        { 
          connect().then((result)=>{
           // console.log(result.web3.chainId)
          //setErrorMessage(result.errorMessage);
         // setAcounts(result.acounts)
            if(result.error==false){
             /* alert(readPublicKey())*/
              dispatch({ type: "connectionChange", publicKey:result.acounts,web3:result.web3});
              dispatch({type: "totalShowChange", totalShow:true});
              window.ethereum.on('accountsChanged', function (accounts) {
               
                dispatch({ type: "connectionChange", publicKey:null,web3:null});
          dispatch({type: "totalShowChange", totalShow:false});
         
              })
              
            }
          });
        }
        else{
          
          dispatch({ type: "connectionChange", publicKey:null,web3:null});
          dispatch({type: "totalShowChange",totalShow:false}); 
          
        }
        
        }

      }
  
        
        >{text}</button></div>
        </div>
        
        <div>{errorMessage}</div>
        <div>{acounts}</div>
        
      </div>
     
    </div> 
    </div>
  );
}
export default Header;
