import styles from "../css/Timer.module.css";
import classnames from "classnames";
import React, { useState,useEffect } from "react";
import stable from "../assets/stablefarms.pdf"
function Timer()
{
  const [remainingTime, setRemainingTime] = useState(0);
  
var remain=0;
  useEffect(() => {

    timeFetch();
    const timer =setInterval(() => {
      if(remain>0 && Number.isNaN(remain)==false)
      remain=remain-1;
      else 
      remain=0;
      setRemainingTime(getTimeRemaining2(remain));
    }, 1000);
    return () => clearInterval(timer);
  }, []);
 
  function timeFetch()
    {
        //fetch("https://api.bscscan.com/api?module=block&action=getblockcountdown&blockno=10076550&apikey=PUCYWP94ZIN3TPQMVX22GJ52FNBU6XZE9Q")
        fetch("https://api.bscscan.com/api?module=block&action=getblockcountdown&blockno=10243800&apikey=PUCYWP94ZIN3TPQMVX22GJ52FNBU6XZE9Q")
        .then(response => response.json())
        .then((jsonData) => {
            remain=jsonData.result.EstimateTimeInSec;  
                
        })
        .catch((error) => {
          
          //remain=getTimeRemaining("Mon Aug 16 2021 10:15:44 GMT").total/1000;
          remain=0;//getTimeRemaining("Mon Aug 16 2021 10:15:44 GMT").total/1000;
        })
    }

    return(
        <div className={classnames(styles.boxTime)}>
        {/*<div className={styles.farming}>FARMS LAUNCH IN</div>*/}
        <div className={styles.farming}>Harvest Lockup Ends In:</div>
        <div className={styles.row}>
          <div className={classnames(styles.c1, styles.c1_s, styles.c1_m)}>
            <div className={styles.circle}>{remainingTime.days} Days</div>{" "}
          </div>
          <div className={classnames(styles.c2, styles.c2_s, styles.c2_m)}>
            <div className={styles.circle}>{remainingTime.hours} Hours</div>{" "}
          </div>
          <div className={classnames(styles.c3, styles.c3_s, styles.c3_m)}>
            <div className={styles.circle}>{remainingTime.minutes} Minutes</div>{" "}
          </div>
          <div className={classnames(styles.c4, styles.c4_s, styles.c4_m)}>
            <div className={styles.circle}>{remainingTime.seconds} Seconds</div>{" "}
          </div>
        </div>
        <div className={styles.btnbox}><button className={styles.btn} onClick={()=>{window.open(stable, "_blank")}}><i className="fa fa-info"></i> Learn More</button></div>
      </div>
      
    );
}
//Time Start
function getTimeRemaining(endtime) {
  var t = Date.parse(endtime) - Date.parse(new Date());
  var seconds = Math.floor((t / 1000) % 60);
  var minutes = Math.floor((t / 1000 / 60) % 60);
  var hours = Math.floor((t / (1000 * 60 * 60)) % 24);
  var days = Math.floor(t / (1000 * 60 * 60 * 24));
  return {
    'total': t,
    'days': days,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
}

function getTimeRemaining2(remainTimeInSec) {

  var seconds = Math.floor((remainTimeInSec%(24*3600)%3600)%60);
  var minutes = Math.floor((remainTimeInSec%(24*3600)%3600)/60);
  var hours = Math.floor((remainTimeInSec%(24*3600))/3600)
  var days = Math.floor(remainTimeInSec / (24*3600));
  return {
    
    'days': days,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
}


export default Timer;