import { useEffect, useState } from "react";
import BigNumber from "bignumber.js";
import {web3, tokenContracts, fstableContract, masterchefContract} from "../utils/web3_http.js";
const BIG_TEN = new BigNumber(10);
const useDebugConsole = false;

//Load the token price by given token contract address
//it uses pancakswap api v2 to fetch the price
const loadPrice = async (address) => {
  const response = await fetch(
    "https://api.pancakeswap.info/api/v2/tokens/" + address
  );
  const jsonData = await response.json();
  const price = await jsonData.data.price;
  return price;
};

//keeping the pool id for token combinations
//example BUSD/STABLE is index 5 with stable:0, busd:2
const priceCombinations = [
    [0],
    [1],
    [2],
    [3],
    [0,1],
    [0,2],
    [0,13],
    [1,2],
    [2,14],
    [1,14],
    [1,3],
    [2,3],
    [2,13]
];

//gets the token price by poolid
//loads 1 token price for pools, 2 token prices for farms and adds them up
//requires a better solution for LP pricing
export const getTokenPrice = async (pid) => {
    const tokenIds = priceCombinations[pid];
    const price1 = await loadPrice(tokenContracts[tokenIds[0]]);

    if(tokenIds.length === 1) return String(price1).substring(0, 5);

    const price2 = await loadPrice(tokenContracts[tokenIds[1]]);
    const price = new BigNumber(price1).plus(price2);
    return String(price).substring(0, 5);
};

//gets FSTABLE token price
export const getFSTABLEPrice = async () => {
    const price = await getTokenPrice(0);
    return price;
}

//gets the Fstable balance of given account address
export const getStableInWallet = async (accountAddress) => {
    if(accountAddress == null){
      if(useDebugConsole) console.log("wallet is not connected...");
      return null;
    }

    if(useDebugConsole) console.log("getting FSTABLE balance of: "+accountAddress);
    const balance = await fstableContract.methods.balanceOf(accountAddress).call();
    if(useDebugConsole) console.log(balance);

    const amount = new BigNumber(balance).dividedBy(BIG_TEN.pow(18));
    return amount.toString();
};


//gets the stake amount from UserInfo struct
//might need update after bscscan api subscription
export const getUserStakesInPool = async (accountAddress, pid) => {
  if(accountAddress == null){
    if(useDebugConsole) console.log("wallet is not connected...");
    return null;
  }

  if(useDebugConsole) console.log("getting staked amount in pool: "+pid);
  const userInfo = await masterchefContract.methods.userInfo(pid, accountAddress).call();
  //const amount = web3.utils.fromWei(BigNumber(userInfo.amount).toString()).toString();
  const amount = new BigNumber(userInfo.amount).dividedBy(BIG_TEN.pow(18));
  if(useDebugConsole) console.log(amount);
  return amount.toString();
};


//gets the reward amount from UserInfo struct
//might need update after bscscan api subscription
export const getRewardLockedUpInPool = async (accountAddress, pid) => {
    if(accountAddress == null){
      if(useDebugConsole) console.log("wallet is not connected...");
      return null;
    }

    if(useDebugConsole) console.log("getting rewards locked up in pool: "+pid);
    const userInfo = await masterchefContract.methods.pendingFstable(pid, accountAddress).call();
    const reward = new BigNumber(userInfo).dividedBy(BIG_TEN.pow(18)).toString().substring(0, 5);
    return reward;
};

//gets and sums up all reward tokens of a user in all pools/farms
export const getAllRewardsLockedUp = async (accountAddress) => {
    let reward = new BigNumber(0);

    for(let i=0; i<13;i++)
    {
        const amount = await getRewardLockedUpInPool(accountAddress, i);
        reward = reward.plus(new BigNumber(amount));
    }
    return Math.floor(reward).toString();
};

export const getBalanceNumber = (amount, decimals = 18) => {
    return Math.floor(new BigNumber(amount).dividedBy(BIG_TEN.pow(decimals)));
};

//gets the liqudity of a pool/farm from the custom backend running on google firebase
export const getPoolLiquidity = async (pid) => {
    const response = await fetch(
        "https://stablefarms-87535-default-rtdb.europe-west1.firebasedatabase.app/liquidity.json"
    );
    const jsonData = await response.json();

    const res = jsonData.poolValues[pid.toString()];
    if(res == null) return 1;

    const amount = new BigNumber(res).dividedBy(BIG_TEN.pow(18));
    return Math.floor(amount).toString();
  };


//hook total fstable token supply amount to home page data
export const useTotalSupply = () => {
  const [totalSupply, setTotalSupply] = useState("");

  useEffect(() => {
    async function fetchTotalSupply() {

        const res = await fstableContract.methods.totalSupply().call();
        setTotalSupply(getBalanceNumber(res.toString()));
    }

    fetchTotalSupply();
  }, []);

  return totalSupply;
};

//hook burned token amount to home page data
export const useBurnedBalance = () => {
  const [balance, setBalance] = useState("");

  useEffect(() => {
    let burnedAmount;

    const fetchBalance = async () => {
      const resultAddr1 = await fstableContract.methods.balanceOf(
        "0xaaEf057B1969354865Ecf7A06B620de615237D0c"
      ).call();

      const resultAddr2 = await fstableContract.methods.balanceOf(
        "0x000000000000000000000000000000000000dEaD"
      ).call();

      burnedAmount =
        getBalanceNumber(resultAddr1.toString()) +
        getBalanceNumber(resultAddr2.toString());
      setBalance(burnedAmount);
    };

    fetchBalance();
  }, []);

  return balance;
};

//hook fstable token price to home page data
export const useFstablePrice = () => {
  const [price, setPrice] = useState("");

  useEffect(() => {
    async function fetchData() {     
      getFSTABLEPrice().then( p => {
        setPrice(String(p).substring(0, 5));
      });
    }
    fetchData();
  }, []);

  return price;
};


//hook TVL amount to home page data
export const useTVL = () => {
  const [tvl, setTvl] = useState("");

  useEffect(() => {
    const fetchData = async () => {
        const response = await fetch(
            "https://stablefarms-87535-default-rtdb.europe-west1.firebasedatabase.app/liquidity.json"
        );
        const jsonData = await response.json();
    
        const total = jsonData.total;
        const amount = new BigNumber(total).dividedBy(BIG_TEN.pow(18));
        if (amount) setTvl(Math.floor(amount).toString());
    };

    fetchData();
  }, []);

  return tvl;
};
