import styles from "../css/Cart.module.css";
import classnames from "classnames";
import React, { useState, useEffect } from "react";
import { useDispatch , useSelector} from "react-redux";
import {connect, readPublicKey,approve,deposit,withdraw,harvest,allowance,balanceOf,getLinkAddress} from "./integeration.js"
import {getTokenPrice, getPoolLiquidity, getRewardLockedUpInPool, getUserStakesInPool} from "./hooks/getData.js"
import {getFarmApr, getPoolApr} from "./hooks/getApr.js"

//farms
import StableUsdt from "../assets/STABLE-USDT.png";
import StableUsdc from "../assets/Stable-USDC.png";
import UsdcUsdt from "../assets/USDC-USDT.png";
import BusdStable from "../assets/BUSD-STABLE.png";
import BusdUsdt from "../assets/BUSD-USDT.png";
import DaiStable from "../assets/DAI-STABLE.png";
import DaiUsdt from "../assets/DAI-USDT.png";
import UsdcBusd from "../assets/USDC-BUSD.png";
import BusdDai from "../assets/BUSD-DAI.png";
import DripFst from "../assets/Drip-FST.png";
import DripBusd from "../assets/Drip-BUSD.png";
//pools
import BUSD from "../assets/BUSD.png";
import STABLE from "../assets/STABLE.png";
import USDC from "../assets/USDC.png";
import USDT from "../assets/USDT.png";
import DAI from "../assets/DAI.png";
import DRIP from "../assets/DRIP.png";
import BSTABLE from "../assets/BSTABLE.png";




function Cart(props, state) {

  const dispatch = useDispatch();

  const web3=useSelector(state => state.web3)
  const publicKey=useSelector(state => state.publicKey)
  const totalShow=useSelector(state => state.totalShow)

  const [show, setShow] = useState(false);
 const [balance,setBalance]=useState("");
  const [amount, setAmount] = useState(0);
const [approveStatus,setApproveStatus]=useState(false);

const [price, setPrice] = useState(0);
  const [apr, setApr] = useState(0);
  const [liquidity, setLiquidity] = useState(0);

  const[staked,setStaked] = useState(0);
  const[reward, setReward] = useState(0);

  useEffect(() => {
    async function fetchData() {
      const priceVal = await getTokenPrice(props.pid);
      setPrice(priceVal.toString());

      const liquidityVal = await getPoolLiquidity(props.pid);
      setLiquidity(parseFloat(liquidityVal).toFixed(2));

      //check if the cart belongs to a farm or a pool
      //this should pass from Farm.js or Pool.js but for now we can
      //check the poolId
      let aprV;
      if(props.pools=="true"){ aprV = await getPoolApr(props.pid);console.log(props.pid)}
      else aprV = await getFarmApr(props.pid);
      setApr(aprV.toString());
    }
    fetchData();
  }, []);

  useEffect(() => {
    async function fetchData() {
      //check the earned reward amount only if the wallet is connected
      //and we have the address available
      if(publicKey != null)
      {
        const rewards = await getRewardLockedUpInPool(publicKey, props.pid);
        setReward(rewards);
        const stakedVal = await getUserStakesInPool(publicKey, props.pid);
        setStaked(stakedVal);
      }
    }
    fetchData();
  }, [publicKey]);

  
  if(web3!=null)
  {

    if(approveStatus==false)
    {
    allowance(publicKey,props.pid).then((flag)=>{
      if(flag==true)
      {
        
        setApproveStatus(flag);
        balanceOf(publicKey,web3,props.pid).then((result)=>{
          setBalance(result);
        });
        setShow(true);
      }
    })
  }
  }

  var showCss;
  if(show==true&&totalShow==true)
  {
    showCss=null
  }
  else
  {
    showCss=styles.hide;
  }

  var btnText="Unlock Wallet"
  if(web3!=null)
  {
    if(approveStatus!=false)
    {
    btnText="Deposit";
 
  }
    else
    {
  btnText="Approve";
    }
  }
  

window["StableUsdt"]=StableUsdt;
window["StableUsdc"]=StableUsdc;
window["UsdcUsdt"]=UsdcUsdt;
window["BusdStable"]=BusdStable;
window["BusdUsdt"]=BusdUsdt;
window["DaiStable"]=DaiStable;
window["DaiUsdt"]=DaiUsdt;
window["UsdcBusd"]=UsdcBusd;
window["BusdDai"]=BusdDai;
window["DripFst"]=DripFst;
window["DripBusd"]=DripBusd;
window["BUSD"]=BUSD;
window["STABLE"]=STABLE;
window["USDC"]=USDC;
window["USDT"]=USDT;
window["DAI"]=DAI;
window["DRIP"]=DRIP;
window["BSTABLE"]=BSTABLE;



var iconCSS=styles.icon1;
if(props.pools=="true")
iconCSS=styles.icon2;

var hide;
if(props.fees=="hide") hide=styles.hide;

  var cssTheme = styles.boxViolet; var cssDiv2=styles.div2Violet; var cssBox2=styles.box2Violet; var cssBtn=styles.btn2Violet;
  if (props.value == 2) {cssTheme = styles.boxBlue; cssDiv2=styles.div2Blue; cssBox2=styles.box2Blue; cssBtn=styles.btn2Blue;}
  else 
  if (props.value == 3) {cssTheme = styles.boxGreen;cssDiv2=styles.div2Green;cssBox2=styles.box2Green;cssBtn=styles.btn2Green;}
  else 
  if (props.value == 4) {cssTheme = styles.boxCherry;cssDiv2=styles.div2Cherry;cssBox2=styles.box2Cherry;cssBtn=styles.btn2Cherry;}
  else 
  if (props.value == 5) {cssTheme = styles.boxMango;cssDiv2=styles.div2Mango;cssBox2=styles.box2Mango;cssBtn=styles.btn2Mango;}
  else 
  if (props.value == 6) {cssTheme = styles.boxSky;cssDiv2=styles.div2Sky;cssBox2=styles.box2Sky;cssBtn=styles.btn2Sky;}

  const [showDetail, setShowDetail] = useState(false);
  var cssHideDetail1;
  var cssHideDetail2;
  !showDetail ? (cssHideDetail1 = styles.hide) : (cssHideDetail2 = styles.hide);

  return (
    <div className={cssTheme}>
      <div className={styles.row1}>
        <div className={styles.headerText}>{props.text}</div>
        <div>
          <img src={window[props.icon]} className={iconCSS}></img>
        </div>
      </div>
      <div className={styles.row2}>
        <div className={classnames(styles.div1,hide)}>
          <i className="fas fa-comment-dollar"></i> No Fees
        </div>
        <div className={cssDiv2}>{props.power}</div>
      </div>
      <div className={styles.row3}>
        <div className={styles.key}>APR</div>
        <div className={styles.value}>
          <i className="fas fa-info-circle"></i> {apr.toLocaleString()} %
        </div>
      </div>
      <div className={styles.row3}>
        <div className={styles.key}>EARN</div>
        <div className={styles.value}>STABLE</div>
      </div>
      <div className={styles.row3}>
        <div className={styles.key}>Deposit Fee</div>
        <div className={styles.value}>{props.deposit}</div>
      </div>
      <div className={styles.row3}>
        <div className={styles.key}>Withdrawal Fee </div>
        <div className={styles.value}>{props.withdrawal}</div>
      </div>
      <div className={styles.row3}>
        <div className={styles.key}>{props.text} LP Price:</div>
        <div className={styles.value}> {price}</div>
      </div>
      <div className={cssBox2}>
        <div className={styles.header2}>STABLE Earn:</div>
        <div className={styles.num}>{reward}</div>
        <div>
          <button className={styles.btn1} disabled={!totalShow} onClick={()=>{
            harvest(publicKey,web3,props.pid);
            balanceOf(publicKey,web3,props.pid).then((result)=>{
              setBalance(result);
            });
            
          }}>Harvest</button>
        </div>
      </div>
      <div className={styles.USDT}>{props.text} LP Staked:  {staked}</div><br/>
      <div>
      <div className={classnames(showCss)}>LP balance in your wallet: {balance}</div>
        <input type="number" pattern="[0-9]*" placeholder="Amount" className={classnames(showCss,styles.amountTxt)} onChange={(event)=>{setAmount(event.target.value)}}></input>
        
        <button className={cssBtn} onClick={()=>{
          if(btnText=="Unlock Wallet")
          {
            connect().then((result)=>{
              if(result.error==false){
                
                
                dispatch({ type: "connectionChange", publicKey:result.acounts,web3:result.web3});
                dispatch({type: "totalShowChange",totalShow:true});
                window.ethereum.on('accountsChanged', function (accounts) {
               
                  dispatch({ type: "connectionChange", publicKey:null,web3:null});
                  dispatch({type: "totalShowChange", totalShow:false});
           
                })
                
              }
            });
          }
          else if(btnText=="Approve")
          {

           
            approve(publicKey,web3,props.pid).then(()=>{
              setApproveStatus(true);
              balanceOf(publicKey,web3,props.pid).then((result)=>{
                setBalance(result);
              });
            setShow(true);});

            
          }
          else if(btnText=="Deposit")
          {
           
          if(!isNaN(amount))
          {
          deposit(publicKey,web3,amount,props.pid);
          balanceOf(publicKey,web3,props.pid).then((result)=>{
            setBalance(result);
          });
          }
         
          }
          
        }}>{btnText}</button>

<button className={classnames(cssBtn,showCss)} onClick={()=>{
withdraw(publicKey,web3,amount,props.pid);
balanceOf(publicKey,web3,props.pid).then((result)=>{
  setBalance(result);
});
}}>Withdraw</button>

      </div>
      <div
        className={styles.detail}
        onClick={() => {
          showDetail ? setShowDetail(false) : setShowDetail(true);
        }}
      >
        <span className={cssHideDetail1}>
          Hide <i className="fas fa-angle-up"></i>
        </span>
        <span className={cssHideDetail2}>
          Details <i className="fas fa-angle-down"></i>
        </span>
      </div>
      <div className={classnames(styles.box3, cssHideDetail1)}>
        <div className={styles.row3}>
          <div className={styles.key}> Deposit  </div>
          <div className={styles.value}>
            {props.text} <i className="fas fa-fighter-jet"></i>
          </div>
        </div>
        <div className={styles.row3}>
          <div className={styles.key}> Total Liquidity </div>
          <div className={styles.value}> $ {liquidity} </div>
        </div>
        <div className={styles.view} onClick={()=>{window.open(getLinkAddress(props.pid), "_blank")}}>View on BscScan</div>
      </div>
    </div>
  );
}
export default Cart;
